/* -------------------------------------------------------------- */
/* Mobile CSS */
.bird-dashboard {
    max-height: 600px;
    overflow-y: auto;
    border: 2px solid #0f4e4a;
    border-radius: 12px;
    padding: 10px 8px;
    margin: 20px 15px;
    background: #f9f9f9;
    width: 90%;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

.bird-dashboard h2 {
    color: #0f4e4a;
    margin-bottom: 12px;
    font-size: 20px;
}

/* Toggle Buttons */
.bird-toggle-buttons {
    margin-bottom: 12px;
}

.bird-toggle-buttons button {
    margin-right: 8px;
    background: #ccc;
    color: white;
    border: none;
    padding: 6px 12px;
    border-radius: 6px;
    cursor: pointer;
    transition: background 0.2s ease;
    font-size: 11px;
}

.bird-toggle-buttons button.active {
    background: #0f4e4a;
}

/* Year Selector */
.year-selector {
    margin-bottom: 12px;
}

.year-selector select {
    padding: 4px 8px;
    border-radius: 6px;
}

/* Table Container */
.bird-table-container {
    max-height: 400px;
    overflow-y: auto;
    border: 1px solid #ddd;
    border-radius: 8px;
    background: white;
}

/* Table Styling */
.bird-table {
    width: 100%;
    border-collapse: collapse;
    font-size: 14px;
}

.bird-table thead {
    background-color: #0f4e4a;
    color: white;
}

.bird-table th,
.bird-table td {
    vertical-align: middle;
    padding: 6px 8px;
}

.bird-table th:first-child,
.bird-table td:first-child {
    text-align: left;
    width: 40%;
}

.bird-table th:nth-child(2),
.bird-table td:nth-child(2) {
    text-align: center;
    width: 30%;
}

.bird-table th:last-child,
.bird-table td:last-child {
    text-align: center;
    width: 30%;
}

/* Chart Area */
.bird-chart {
    height: 400px;
}

/* searching box  */
.bird-search {
    margin-bottom: 12px;
}

.bird-search input {
    width: 100%;
    padding: 6px 10px;
    border-radius: 6px;
    border: 1px solid #ccc;
    box-sizing: border-box;
}

/* Scrolling for family chart on mobile */
.bird-family-chart {
    width: 100%;
    overflow-x: auto;
    padding-bottom: 1rem;
}

.bird-family-chart .recharts-wrapper {
    min-width: inline-block;
}

