/* -----------------------------------------
Mobile CSS */
.main-container{
    width: 100%;
    height: 100%;
    flex-shrink: 0;
    margin:0;
    padding:0;
}

.background-title{
    color: #000;
    font-family: Inter;
    font-size: 32px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    letter-spacing: -0.96px;
    text-decoration-line: underline;
    text-decoration-style: solid;
    text-decoration-skip-ink: none;
    text-decoration-thickness: auto;
    text-underline-offset: auto;
    text-underline-position: from-font;
    padding-left: 40px;
    margin-top: 50px;
}

.content{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 20px;
}

.habitats{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    align-self: stretch;

    color: #828282;
    font-family: Inter;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 150%; /* 36px */
    padding-left: 40px;
}

.hobbies{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    align-self: stretch;

    color: #828282;
    font-family: Inter;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 150%; /* 36px */
    padding-left: 40px;
    padding-top: 50px;
}

.history{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    align-self: stretch;

    color: #828282;
    font-family: Inter;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 150%; /* 36px */
    padding-right: 40px;
    padding-top: 50px;
}

.left-image{
    width: 100%;
    height: auto;
    flex-shrink: 0;
    aspect-ratio: 4/3;
    margin-top: 50px;

    background: url("../../photos/951DE5F0-69BC-4B81-8AF5-668B6A986574.JPG") lightgray 50% / cover no-repeat;
    box-shadow: 4px 4px 4px 0 rgba(0, 0, 0, 0.25);
}

.right-image{
    width: 100%;
    height: auto;
    flex-shrink: 0;
    aspect-ratio: 4/3;

    background: url("../../photos/Media%20(43).jpg") lightgray 50% / cover no-repeat;
    box-shadow: 4px 4px 4px 0 rgba(0, 0, 0, 0.25);
}

